textarea {
  resize: none;
}
textarea.resize-vertical {
  resize: vertical;
}
textarea.resize-horizontal {
  resize: horizontal;
}
textarea.resize-vertical.resize-horizontal,
textarea.resize-both {
  resize: both;
}
.text-large {
  font-size: 200%;
}
.hide-outline {
  outline: none;
  -ms-hide-outline: expression(this.hideFocus = true);
}
.hide-outline:focus,
.hide-outline:active,
.hide-outline:focus:active {
  outline: none;
  -ms-hide-outline: expression(this.hideFocus = true);
}
.hide-outline:focus::-moz-focus-inner,
.hide-outline:active::-moz-focus-inner,
.hide-outline:focus:active::-moz-focus-inner {
  outline: none !important;
  border: 0 !important;
}
input::-ms-clear,
input::-ms-reveal {
  display: none !important;
}
