.x-preview-canvas-wrapper {
  position: relative;
  box-sizing: border-box;
  padding: 0;
}
.x-preview-canvas-wrapper.interactive .tartan-preview-control canvas {
  cursor: move;
}
.x-preview-canvas-wrapper .tartan-preview-control {
  position: absolute !important;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.x-preview-canvas-wrapper .tartan-preview-control canvas {
  border: 0;
  margin: 0;
  padding: 0;
  image-rendering: pixelated;
  -ms-interpolation-mode: nearest-neighbor;
}
.x-tartan-list li.tartan-search-input {
  cursor: text;
}
.x-tartan-list li.tartan-search-input > span {
  padding: 0.75em 15px;
  display: block;
  position: relative;
}
.x-tartan-list li.tartan-search-input .form-control-static {
  display: block;
  padding: 0.6em 1em 0.4em;
}
.x-tartan-list li.tartan-search-input.pinned {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
  background: inherit;
  padding-top: 0.75em;
}
.x-tartan-list .dropdown.open {
  background: #f5f5f5;
}
.x-tartan-list .dropdown > h3 {
  cursor: pointer;
  text-transform: none;
  margin: 0;
  padding: 0.5em 0;
  border-bottom: 1px solid #e7e7e7;
}
.x-tartan-list .dropdown > h3 span {
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.x-tartan-list .dropdown .dropdown-menu {
  background: #f5f5f5;
  border: 0;
  border-bottom: 1px solid #e7e7e7;
  border-radius: 0;
  max-height: 500px;
  overflow-y: auto;
  margin: 0;
}
.x-tartan-list .dropdown .dropdown-menu li a {
  line-height: 2em;
  padding-left: 15px;
  padding-right: 15px;
}
.tartan-list {
  margin: -10px;
}
.tartan-list .pagination-control {
  width: auto;
  max-width: 320px;
  margin: 15px auto;
}
.tartan-list .tartan-list-item {
  width: 240px;
  padding: 4px;
  margin: 20px 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
}
.tartan-list .tartan-list-item h4 {
  font-size: 1.2em;
  line-height: normal;
  margin: 10px 0 4px;
}
.tartan-list .tartan-list-item h4 a {
  display: inline-block;
  vertical-align: top;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 230px;
}
.tartan-list .tartan-list-item canvas {
  display: block;
  width: 100%;
  height: 180px;
}
@media (min-width: 768px) {
  .modal.modal-fluid .modal-dialog {
    width: auto;
    margin: 30px 5%;
  }
}
